
.vux-flexbox{
  text-align:left;
  display:-webkit-box;
  display:-webkit-flex;
  -webkit-box-align:center;
  -webkit-align-items:center;
}

.vux-flexbox .vux-flexbox-item{
  -webkit-box-sizing:border-box;
  -webkit-box-flex:1;
  -webkit-flex:1;
  margin-left:8px;
  min-width:20px;
}

.vux-flexbox .vux-flexbox-item:first-child{
  margin-left:0;
}

.vux-flexbox[vuxe-mode~=average] .vux-flexbox-item{
  width:100%;
}



